import React, {useEffect, useMemo} from 'react';
import {useStore} from "../../../store";
import {get} from "lodash";
import GridView from "../../../containers/grid-view/grid-view";
import {KEYS} from "../../../constants/key";
import {URLS} from "../../../constants/url";
import Field from "../../../containers/form/field";

const ProductsContainer = ({...rest}) => {
    const setBreadcrumbs = useStore(state => get(state, 'setBreadcrumbs', () => {
    }))
    const breadcrumbs = useMemo(() => [
        {
            id: 1,
            title: 'Продукты',
            path: '/products',
        },
        {
            id: 2,
            title: 'Все продукты',
            path: '/products/all',
        }
    ], [])

    useEffect(() => {
        setBreadcrumbs(breadcrumbs)
    }, [])

    const ModalBody = ({data, rowId = null}) => <>
        <Field name={'name'} type={'input'} label={'Название продукта'} defaultValue={rowId ? get(data, 'name') : null}
               params={{required: true}}/>
    </>
    return (
        <>
            <GridView
                ModalBody={ModalBody}
                tableHeaderData={[
                    {
                        id: 2,
                        key: 'productname',
                        title: 'Наименование продукта'
                    },
                    {
                        id: 3,
                        key: 'typeofpolice',
                        title: 'Тип страховщика'
                    },
                    {
                        id: 4,
                        key: 'typeofpayment',
                        title: 'Тип оплаты'
                    },
                    {
                        id: 5,
                        key: 'typeofinsurerId',
                        title: 'Страхователь'
                    },
                    {
                        id: 6,
                        key: 'typeofpolice',
                        title: 'Формат полиса'
                    },
                    {
                        id: 7,
                        key: 'fixedpremium',
                        title: 'Страховая сумма'
                    },
                ]}
                keyId={KEYS.products}
                url={URLS.products}
                title={'Все продукты'}
                responseDataKey={'products'}
                viewUrl={'/products/view'}
                createUrl={'/products/view'}
                updateUrl={'/products/view'}

            />
        </>
    );
};

export default ProductsContainer;