import React, {useState} from 'react';
import {Col, Row} from "react-grid-system";
import StepNav from "../../../../components/step-nav";
import Field from "../../../../containers/form/field";
import Form from "../../../../containers/form/form";
import Button from "../../../../components/ui/button";
import {useSettingsStore} from "../../../../store";
import {get, includes, some, values, isEmpty, isEqual} from "lodash"
import {useGetAllQuery} from "../../../../hooks/api";
import {KEYS} from "../../../../constants/key";
import {URLS} from "../../../../constants/url";
import {getSelectOptionsListFromData} from "../../../../utils";
import Title from "../../../../components/ui/title";
import {toast} from "react-toastify";
import Table from "../../../../components/table";
import {Trash2} from "react-feather";

const StepOne = ({...props}) => {

    const [riskItem, setRiskItem] = useState({});

    const setProduct = useSettingsStore(state => get(state, 'setProduct', () => {
    }))
    const resetProduct = useSettingsStore(state => get(state, 'resetProduct', () => {
    }))
    const addRiskList = useSettingsStore(state => get(state, 'addRiskList', () => {
    }))
    const removeRiskList = useSettingsStore(state => get(state, 'removeRiskList', () => {
    }))
    const riskList = useSettingsStore(state => get(state, 'riskList', []))

    const product = useSettingsStore(state => get(state, 'product', {}))


    const nextStep = ({data}) => {
        setProduct(data);
        props.nextStep();
    }

    const prevStep = () => {
        props.previousStep();
    }

    const reset = () => {
        resetProduct();
        props.firstStep();
    }

    let {data: groups} = useGetAllQuery({key: KEYS.groupsofproducts, url: URLS.groupsofproducts})
    groups = getSelectOptionsListFromData(get(groups, `data.data`, []), '_id', 'name')

    let {data: subGroups} = useGetAllQuery({key: KEYS.subgroupsofproducts, url: URLS.subgroupsofproducts})
    subGroups = getSelectOptionsListFromData(get(subGroups, `data.data`, []), '_id', 'name')

    let {data: insurances} = useGetAllQuery({key: KEYS.typeofinsurer, url: URLS.typeofinsurer})
    insurances = getSelectOptionsListFromData(get(insurances, `data.data`, []), '_id', 'name')

    let {data: sectors} = useGetAllQuery({key: KEYS.typeofsector, url: URLS.typeofsector})
    sectors = getSelectOptionsListFromData(get(sectors, `data.data`, []), '_id', 'name')

    let {data: persons} = useGetAllQuery({key: KEYS.typeofpersons, url: URLS.typeofpersons})
    persons = getSelectOptionsListFromData(get(persons, `data.data`, []), '_id', 'name')

    let {data: status} = useGetAllQuery({key: KEYS.statusofproduct, url: URLS.statusofproduct})
    status = getSelectOptionsListFromData(get(status, `data.data`, []), '_id', 'name')

    let {data: riskGroups} = useGetAllQuery({key: KEYS.typeofrisk, url: URLS.typeofrisk})
    riskGroups = getSelectOptionsListFromData(get(riskGroups, `data.data`, []), '_id', 'name')

    let {data: risks} = useGetAllQuery({key: KEYS.risk, url: URLS.risk})
    risks = getSelectOptionsListFromData(get(risks, `data.data`, []), '_id', 'name')

    let {data: insuranceClasses} = useGetAllQuery({key: KEYS.classes, url: URLS.classes})
    insuranceClasses = getSelectOptionsListFromData(get(insuranceClasses, `data.data`, []), '_id', 'name')

    const setRisk = (value, name) => {
        if (includes(['riskgroup', 'classId', 'risk'], name)) {
            setRiskItem(prev => ({...prev, [name]: value}))
        }
    }

    const addRiskItem = () => {

        if (some(values(riskItem), val => isEmpty(val))) {
            toast.warn('You have to select all fields')
        } else {
            addRiskList({...riskItem, id: riskList.length + 1})
            setRiskItem({riskgroup: '', classId: '', risk: ''})
        }
    }

    const findItem = (list = [], id = null) => {
        return list.find(item => isEqual(get(item, 'value'), id))
    }

    return (
        <Row>
            <Col xs={12}>
                <StepNav step={1}/>
            </Col>
            <Col xs={12}>
                <Form formRequest={nextStep} getValueFromField={setRisk}>
                    <Row>
                        <Col xs={3}>
                            <Field label={'Выберите категорию '} options={groups} type={'select'}
                                   name={'groupofproductsId'} params={{required: true}}
                                   property={{hasRequiredLabel: true}} defaultValue={'629712ce70bda308f5ab0c94'}/>
                        </Col>
                        <Col xs={3}>
                            <Field label={'Выберите подкатегорию '} options={subGroups} type={'select'}
                                   name={'subgroupofproductsId'} params={{required: true}}
                                   property={{hasRequiredLabel: true}}/>
                        </Col>
                        <Col xs={3}>
                            <Field label={' Наименование продукта'} type={'input'} name={'productname'}
                                   params={{required: true}}
                                   property={{hasRequiredLabel: true, placeholder: 'Введите значение'}}/>
                        </Col>
                        <Col xs={3}>
                            <Field label={'Код назначения'}
                                   type={'input'}
                                   name={'codeproduct'}
                                   property={{placeholder: 'Введите значение'}}
                                   defaultValue={get(product,'codeproduct','')}
                            />
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={3}>
                            <Field
                                label={'Работа по версии продукта (Версия продукта)'}
                                type={'input'}
                                name={'versionproduct'}
                                property={{placeholder: 'Введите значение'}}
                                defaultValue={get(product,'versionproduct','')}
                            />
                        </Col>
                        <Col xs={3}>
                            <Field options={insurances} label={'Форма страхования'} type={'select'}
                                   name={'typeofinsurerId'}/>
                        </Col>
                        <Col xs={3}>
                            <Field options={sectors} label={'Указать сектор'} type={'select'} name={'typeofsectorId'}/>
                        </Col>
                        <Col xs={3}>
                            <Field label={'Требуется разрешение'} type={'switch'} name={'isrequirepermission'}
                                   defaultValue={false}/>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={9}>
                            <Field options={persons} label={'Выбрать тип страховщика'} type={'checkbox'}
                                   name={'isrequirepermission'} defaultValue={false} params={{required: true}}/>
                        </Col>
                        <Col xs={3}>
                            <Field options={status} label={'Статус продукта'} type={'switch'}
                                   name={'isrequirepermission'} defaultValue={false} params={{required: true}}
                                   property={{hasRequiredLabel: true}}/>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={12} className={'mb-15'}>
                            <Title>Добавить риски</Title>
                        </Col>
                        <Col xs={12} className={'mb-15'}>
                            <Row>
                                <Col xs={10}>
                                    <Row align={'center'}>
                                        <Col xs={4}>
                                            <Field
                                                options={riskGroups}
                                                type={'select'}
                                                name={'riskgroup'}
                                                defaultValue={get(riskItem, 'riskgroup')}
                                                property={{
                                                    hideLabel: true,
                                                    placeholder: 'Выберите группу  риска'
                                                }}/>
                                        </Col>
                                        <Col xs={4}>
                                            <Field
                                                options={risks}
                                                type={'select'}
                                                name={'risk'}
                                                defaultValue={get(riskItem, 'risk')}
                                                property={{
                                                    hideLabel: true,
                                                    placeholder: 'Выберите риск'
                                                }}/>
                                        </Col>
                                        <Col xs={4}>
                                            <Field
                                                options={insuranceClasses}
                                                type={'select'}
                                                name={'classId'}
                                                defaultValue={get(riskItem, 'classId')}
                                                property={{
                                                    hideLabel: true,
                                                    placeholder: 'Класс страхования'
                                                }}/>
                                        </Col>
                                    </Row>
                                </Col>
                                <Col xs={2} className={'text-right'}>
                                    <Button onClick={addRiskItem} type={'button'}>Добавить</Button>
                                </Col>
                                {riskList.length > 0 && <Col xs={12}>
                                    <hr/>
                                    <Table thead={[1, 2, 3, 4]}>
                                        {riskList.map((item, i) => <tr key={i + 1}>
                                            <td>{get(findItem(riskGroups, get(item, 'riskgroup')), 'label', '-')}</td>
                                            <td>{get(findItem(risks, get(item, 'risk')), 'label', '-')}</td>
                                            <td>{get(findItem(insuranceClasses, get(item, 'classId')), 'label', '-')}</td>
                                            <td className={'cursor-pointer'}
                                                onClick={() => removeRiskList(get(item, 'id', null))}><Trash2
                                                color={'#dc2626'}/></td>
                                        </tr>)}
                                    </Table>
                                </Col>}
                            </Row>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={12} className={'mt-32'}>
                            <Button className={'mr-16'} type={'button'} onClick={reset} danger outlined
                                    back>Отменить</Button>
                            <Button dark className={'mr-16'} type={'button'} onClick={prevStep}
                                    outlined>Назад</Button>
                            <Button type={'submit'} success>Продолжить</Button>
                        </Col>
                    </Row>
                </Form>
            </Col>
        </Row>
    );
};

export default StepOne;