import React, {useEffect, useMemo} from 'react';
import {useStore} from "../../../store";
import {get} from "lodash";
import GridView from "../../../containers/grid-view/grid-view";
import {KEYS} from "../../../constants/key";
import {URLS} from "../../../constants/url";
import Field from "../../../containers/form/field";
import {useGetAllQuery} from "../../../hooks/api";
import {getSelectOptionsListFromData} from "../../../utils";

const RiskContainer = ({...rest}) => {
    const {data:types} = useGetAllQuery({key:KEYS.typeofrisk,url:URLS.typeofrisk})
    const typesOptions = getSelectOptionsListFromData(get(types,`data.data`,[]),'_id','name')
    const setBreadcrumbs = useStore(state => get(state, 'setBreadcrumbs', () => {
    }))
    const breadcrumbs = useMemo(() => [
        {
            id: 1,
            title: 'Справочники',
            path: '/handbook',
        },
        {
            id: 2,
            title: 'Риск',
            path: '/handbook/risk',
        }
    ], [])

    useEffect(() => {
        setBreadcrumbs(breadcrumbs)
    }, [])

    const ModalBody = ({data,rowId = null}) =>  <>
        <Field  name={'name'} type={'input'} label={'Название риска'} defaultValue={rowId ? get(data,'name'):null} params={{required: true}} />
        <Field  name={'typeofrisksId'} type={'select'} label={'Tипа риск '} options={typesOptions} defaultValue={rowId ? get(data,'typeofrisksId'):null} params={{required: true}} />
    </>
    return (
        <>
            <GridView
                ModalBody={ModalBody}
                tableHeaderData={[
                    {
                        id: 2,
                        key: 'name',
                        title: 'Название риска'
                    },
                    {
                        id: 3,
                        key: 'typeofrisksId',
                        title: 'Tипа риска'
                    },
                ]}
                keyId={KEYS.risk}
                url={URLS.risk}
                title={'Риск'}
                responseDataKey={'data'}

            />
        </>
    );
};

export default RiskContainer;