import React from 'react';
import {Col, Row} from "react-grid-system";
import StepNav from "../../../../components/step-nav";
import Field from "../../../../containers/form/field";
import Form from "../../../../containers/form/form";
import Button from "../../../../components/ui/button";
import {useSettingsStore} from "../../../../store";
import {get} from "lodash"
import Flex from "../../../../components/flex";

const StepTwo = ({...props}) => {
    const setProduct = useSettingsStore(state => get(state, 'setProduct', () => {
    }))
    const resetProduct = useSettingsStore(state => get(state, 'resetProduct', () => {
    }))

    const nextStep = ({data}) => {
        // setProduct(data);
        // props.nextStep();
    }

    const prevStep = () => {
        props.previousStep();
    }

    const reset = () => {
        resetProduct();
        props.firstStep();
    }

    return (
        <Row>
            <Col xs={12}>
                <StepNav step={2}/>
            </Col>
            <Col xs={12}>
                <Form formRequest={nextStep}>
                    <Row>
                        <Col xs={4}>
                            <Flex align={'flex-end'}>
                                <Field label={'Имеет форму анкеты заявления'} type={'switch'} name={'isrequirepermission'}
                                       defaultValue={false} property={{hasRequiredLabel:true}}/>
                                <Field label={'Имеет форму анкеты заявления'} type={'dropzone'} name={'isrequirepermission'} property={{hideLabel:true}}/>
                            </Flex>

                        </Col>
                    </Row>
                    <Row>
                        <Col xs={12} className={'mt-32'}>
                            <Button className={'mr-16'} type={'button'} onClick={reset} danger outlined
                                    back>Отменить</Button>
                            <Button dark className={'mr-16'} type={'button'} onClick={prevStep}
                                    outlined>Назад</Button>
                            <Button type={'submit'} success>Продолжить</Button>
                        </Col>
                    </Row>
                </Form>
            </Col>
        </Row>
    );
};

export default StepTwo;