import React, {useEffect, useMemo} from 'react';
import {useStore} from "../../../store";
import {get} from "lodash";
import GridView from "../../../containers/grid-view/grid-view";
import {KEYS} from "../../../constants/key";
import {URLS} from "../../../constants/url";
import Field from "../../../containers/form/field";
import {useGetAllQuery} from "../../../hooks/api";
import {getSelectOptionsListFromData} from "../../../utils";

const AgentsContainer = ({...rest}) => {

    const setBreadcrumbs = useStore(state => get(state, 'setBreadcrumbs', () => {
    }))

    let {data: regions} = useGetAllQuery({key: KEYS.regions, url: URLS.regions})
    regions = getSelectOptionsListFromData(get(regions, `data.data`, []), '_id', 'name')

    let {data: districts} = useGetAllQuery({key: KEYS.districts, url: URLS.districts})
    districts = getSelectOptionsListFromData(get(districts, `data.data`, []), '_id', 'name')

    let {data: persons} = useGetAllQuery({key: KEYS.typeofpersons, url: URLS.typeofpersons})
    persons = getSelectOptionsListFromData(get(persons, `data.data`, []), '_id', 'name')

    const breadcrumbs = useMemo(() => [
        {
            id: 1,
            title: 'Агенты',
            path: '/agents',
        },
        {
            id: 2,
            title: 'Страховые агенты',
            path: '/agents/insurance-agents',
        }
    ], [])

    useEffect(() => {
        setBreadcrumbs(breadcrumbs)
    }, [])

    const ModalBody = ({data, rowId = null}) => <>
        <Field name={'fullName'} type={'input'} label={'Full Name'} defaultValue={rowId ? get(data, 'fullName') : null}
               params={{required: true}}/>
        <Field name={'inn'} type={'input-mask'} label={'INN'} property={{mask: '9999999', maskChar: '_'}}
               defaultValue={rowId ? get(data, 'inn') : null} params={{required: true, pattern: /^[0-9]*$/}}/>
        <Field name={'pin'} type={'input-mask'} label={'PNFL'} property={{mask: '99999999999999', maskChar: '_'}}
               defaultValue={rowId ? get(data, 'pin') : null} params={{required: true, pattern: /^[0-9]*$/}}/>
        <Field name={'typeofpersons'} type={'select'} label={'Person type'} options={persons}
               defaultValue={rowId ? get(data, 'typeofpersons') : null} params={{required: true}}/>
        <Field name={'regionId'} type={'select'} label={'Region'} options={regions}
               defaultValue={rowId ? get(data, 'regionId') : null} params={{required: true}}/>
        <Field name={'districtsId'} type={'select'} label={'District'} options={districts}
               defaultValue={rowId ? get(data, 'districtsId') : null} params={{required: true}}/>
    </>
    return (
        <>
            <GridView
                ModalBody={ModalBody}
                tableHeaderData={[
                    {
                        id: 2,
                        key: 'fullName',
                        title: 'Название'
                    },
                    {
                        id: 3,
                        key: 'inn',
                        title: 'INN'
                    },
                    {
                        id: 4,
                        key: 'pin',
                        title: 'PIN'
                    },
                    {
                        id: 5,
                        key: 'regionId.name',
                        title: 'Region'
                    },
                    {
                        id: 6,
                        key: 'districtsId.name',
                        title: 'District'
                    },
                ]}
                keyId={KEYS.agents}
                url={URLS.agents}
                title={'Страховые агенты'}
                responseDataKey={'data'}

            />
        </>
    );
};

export default AgentsContainer;