import React from 'react';
import {BrowserRouter, Route, Routes, Navigate} from "react-router-dom";
import MainLayout from "../layouts/main-layout";
import DashboardPage from "../modules/dashboard/pages/DashboardPage";
import AuthLayout from "../layouts/auth-layout";
import LoginPage from "../modules/auth/pages/LoginPage";
import SignUpPage from "../modules/auth/pages/SignUpPage";
import IsAuth from "../services/auth/IsAuth";
import IsGuest from "../services/auth/IsGuest";
import BranchesPage from "../modules/handbook/pages/BranchesPage";
import RegionsPage from "../modules/handbook/pages/RegionsPage";
import NotFoundPage from "../modules/auth/pages/NotFoundPage";
import TypeObjectPage from "../modules/handbook/pages/TypeObjectPage";
import ObjectPage from "../modules/handbook/pages/ObjectPage";
import TypePolicePage from "../modules/handbook/pages/TypePolicePage";
import TypeRiskPage from "../modules/handbook/pages/TypeRiskPage";
import RiskPage from "../modules/handbook/pages/RiskPage";
import RolePage from "../modules/handbook/pages/RolePage";
import InsuranceClassPage from "../modules/handbook/pages/InsuranceClassPage";
import InsuranceSubClassPage from "../modules/handbook/pages/InsuranceSubClassPage";
import SectorTypePage from "../modules/handbook/pages/SectorTypePage";
import PolicyFormatsPage from "../modules/handbook/pages/PolicyFormatsPage";
import InsurerTypePage from "../modules/handbook/pages/InsurerTypePage";
import ProductGroupsPage from "../modules/products/pages/ProductGroupsPage";
import ProductSubGroupsPage from "../modules/products/pages/ProductSubGroupsPage";
import ApplicationFormDocsPage from "../modules/handbook/pages/ApplicationFormDocsPage";
import ContractFormPage from "../modules/handbook/pages/ContractFormPage";
import AdditionalDocumentsPage from "../modules/handbook/pages/AdditionalDocumentsPage";
import SettlementClaimTypePage from "../modules/handbook/pages/SettlementClaimTypePage";
import RefundTypePage from "../modules/handbook/pages/RefundTypePage";
import FranchiseTypePage from "../modules/handbook/pages/FranchiseTypePage";
import ProductsPage from "../modules/products/pages/ProductsPage";
import PersonTypePage from "../modules/clients/pages/PersonTypePage";
import ProductStatusPage from "../modules/products/pages/ProductStatusPage";
import AgentsPage from "../modules/agents/pages/AgentsPage";
import ProductCreatePage from "../modules/products/pages/ProductCreatePage";

const Router = ({...rest}) => {
    return (
        <BrowserRouter>

            <IsAuth>
                <Routes>
                    <Route path={"/"} element={<MainLayout/>}>
                        <Route path={"dashboard"} index element={<DashboardPage/>}/>
                        <Route path={"products"}>
                            <Route index path={"all"} element={<ProductsPage/>}/>
                            <Route path={"product-groups"} element={<ProductGroupsPage/>}/>
                            <Route path={"product-subgroups"} element={<ProductSubGroupsPage/>}/>
                            <Route path={"product-status"} element={<ProductStatusPage/>}/>
                            <Route path={"create"} element={<ProductCreatePage/>}/>
                        </Route>
                        <Route path={"clients"}>
                            <Route path={"person-type"} element={<PersonTypePage/>}/>
                        </Route>
                        <Route path={"agents"}>
                            <Route path={"insurance-agents"} element={<AgentsPage/>}/>
                        </Route>
                        <Route path={"handbook"}>
                            <Route index element={<BranchesPage/>}/>
                            <Route path={"branches"} element={<BranchesPage/>}/>
                            <Route path={"regions"} element={<RegionsPage/>}/>
                            <Route path={"object"} element={<ObjectPage/>}/>
                            <Route path={"object-type"} element={<TypeObjectPage/>}/>
                            <Route path={"police-type"} element={<TypePolicePage/>}/>
                            <Route path={"risk-type"} element={<TypeRiskPage/>}/>
                            <Route path={"risk"} element={<RiskPage/>}/>
                            <Route path={"role"} element={<RolePage/>}/>
                            <Route path={"insurance-classes"} element={<InsuranceClassPage/>}/>
                            <Route path={"insurance-subclasses"} element={<InsuranceSubClassPage/>}/>
                            <Route path={"sector-type"} element={<SectorTypePage/>}/>
                            <Route path={"policy-formats"} element={<PolicyFormatsPage/>}/>
                            <Route path={"insurer-type"} element={<InsurerTypePage/>}/>
                            <Route path={"application-form-docs"} element={<ApplicationFormDocsPage/>}/>
                            <Route path={"contract-form"} element={<ContractFormPage/>}/>
                            <Route path={"additional-documents"} element={<AdditionalDocumentsPage/>}/>
                            <Route path={"settlement-claim-type"} element={<SettlementClaimTypePage/>}/>
                            <Route path={"refund-type"} element={<RefundTypePage/>}/>
                            <Route path={"franchise-type"} element={<FranchiseTypePage/>}/>
                        </Route>
                        <Route path={"auth/*"} element={<Navigate to={'/dashboard'} replace/>}/>
                        <Route path={"/"} element={<Navigate to={'/dashboard'} replace/>}/>
                        <Route path={"*"} element={<NotFoundPage/>}/>
                    </Route>

                </Routes>
            </IsAuth>

            <IsGuest>
                <Routes>
                    <Route path={"/auth"} element={<AuthLayout/>}>
                        <Route index element={<LoginPage/>}/>
                        <Route path={"sign-up"} element={<SignUpPage/>}/>
                    </Route>
                    <Route path={"*"} element={<Navigate to={'/auth'} replace/>}/>
                </Routes>
            </IsGuest>

        </BrowserRouter>
    );
};

export default Router;